import type { Operation } from 'slate';

import {
  createTSlatePlugin,
} from 'platejs';
import { Transforms } from "slate"

import type { OtConfig, OtInitOptions, OtOptions, OtStatus } from './types';

import { castArray } from './util';
import { withPlateOt } from './withPlateOt';

export const BaseOtPlugin = createTSlatePlugin<OtConfig>({
  key: 'sharedb',
  priority: 1000, // ËÆæÁΩÆËæÉ‰Ωé‰ºòÂÖàÁ∫ßÔºåÁ°Æ‰øùÂú® NodeId Êèí‰ª∂‰πãÂêéÂä†ËΩΩ
  extendEditor: withPlateOt,
  options: {
    _connection: null,
    _doc: null,
    _operationQueue: [],
    _presence: null,
    _processingQueue: false,
    _socket: null,
    _status: 'disconnected' as OtStatus,
    debug: false,
    enablePresence: false,
  } as unknown as OtOptions & {
    _connection: any;
    _doc: any;
    _operationQueue: (() => Promise<void> | void)[];
    _presence: any;
    _processingQueue: boolean;
    _socket: any;
    _status: OtStatus;
  },
}).extendApi((ctx) => ({
  /**
   * Â∫îÁî®ËøúÁ®ãÊìç‰Ωú
   */
  applyRemoteOperation: (operations: Operation[]) => {
    const { editor, getOptions } = ctx;
    const options = getOptions();
    
    try {
      // ‰ΩøÁî®‰∏ìÁî®ÁöÑËøúÁ®ãÊìç‰ΩúÂ∫îÁî®ÊñπÊ≥ïÔºåÈÅøÂÖçÂæ™ÁéØ
      if ((editor as any).applyRemoteOperations) {
        (editor as any).applyRemoteOperations(operations);
      }
    } catch (error) {
      if (options.debug) {
        console.error('üí• sharedb: Failed to apply remote operation:', error);
      }
      options.onError?.(error);
    }
  },
  /**
   * @description Â∫îÁî®ËøúÁ®ãÊìç‰Ωú-‰∏çÁªèËøáeditor.apply
   * @param operations 
   */
  applyRemoteOperationWithoutEditor: (operations: Operation[]) => {
    const { editor } = ctx as any;
    const updateOp = () => {
      const opsQueue = castArray(operations)
      opsQueue.forEach(op => {
        Transforms.transform(editor as any, op)
      })
      editor.onChange()  
    }
    updateOp()
  },

  /**
   * ËøûÊé•Âà∞ ShareDB ÊúçÂä°Âô®
   */
  connect: async () => {
    const { editor, getOptions, setOption } = ctx;
    const options = getOptions();

    if (options._status === 'connected' || options._status === 'connecting') {
      console.warn('Already connected or connecting');
      return;
    }

    if (!options.sharedb) {
      throw new Error('ShareDB configuration not found. Please call init() first.');
    }

    try {
      setOption('_status', 'connecting');
      options.onStatusChange?.('connecting');

      if (options.debug) {
        console.log('üì° sharedb: Connecting to ShareDB server...');
      }

      // Âä®ÊÄÅÂØºÂÖ• ShareDB Áõ∏ÂÖ≥Ê®°Âùó
      const [{ default: ShareDB }, { default: ReconnectingWebSocket }] = await Promise.all([
        import('sharedb-client-browser/dist/sharedb-client-umd.cjs'),
        import('reconnecting-websocket'),
      ]);

      // Ê≥®ÂÜå sharedb Á±ªÂûã
      if (!ShareDB.types.map.slate) {
        const { default: ottype } = await import('ottype-slate-test');
        ShareDB.types.register(ottype.type);
        
        if (options.debug) {
          console.log('üìã sharedb: Registered slate sharedb type');
        }
      }

      // ÂàõÂª∫ WebSocket ËøûÊé•
      const socket = new ReconnectingWebSocket(options.sharedb.url, [], {
        connectionTimeout: 5000,
        debug: options.debug,
        maxRetries: options.sharedb.reconnection?.maxRetries || 5,
      });

      // ÂàõÂª∫ ShareDB ËøûÊé•
      const connection = new ShareDB.Connection(socket as any);
      const doc = connection.get(options.sharedb.collection || 'documents', options.sharedb.documentId);
  const presence = connection.getDocPresence(options.sharedb.collection || 'documents', options.sharedb.documentId)
      // ËÆæÁΩÆËøûÊé•ÂºïÁî®
      setOption('_presence', presence);
      setOption('_socket', socket);
      setOption('_connection', connection);
      setOption('_doc', doc);

      // ËÆ¢ÈòÖÊñáÊ°£
      await new Promise<void>((resolve, reject) => {
        doc.subscribe((error: any) => {
          if (error) {
            console.error('ShareDB subscription error:', error);
            setOption('_status', 'error');
            options.onError?.(error);
            reject(error as Error);
            return;
          }

          setOption('_status', 'connected');
          options.onStatusChange?.('connected');

          if (options.debug) {
            console.log('‚úÖ sharedb: Connected to ShareDB server');
            console.log('üìÑ sharedb: Document data:', doc.data);
          }

          // ËÆæÁΩÆÂàùÂßãÂÄº
          const initialValue = doc.data?.children || [{
            children: [{
              text: 'hello world',
            }],
            type: 'paragraph',
          }];
          editor.children = initialValue;
          options.onConnect?.();

          resolve();
        });
      });

      // ÁõëÂê¨ËøúÁ®ãÊìç‰Ωú
      doc.on('op', (ops: any[], source: any) => {
        // ÂøΩÁï•Êú¨Âú∞Êìç‰ΩúÁöÑÂõûÊòæÔºàsource ‰∏∫ false Ë°®Á§∫ÊòØÊú¨Âú∞Êìç‰ΩúÔºâ
        if (source) {
          if (options.debug) {
            console.log('üîÑ sharedb: Ignoring local operation echo');
          }
          return;
        }

        if (options.debug) {
          console.log('üì® sharedb: Received remote operations:', ops, 'from source:', source);
        }

        (ctx.api as any).sharedb.applyRemoteOperationWithoutEditor(ops);
      });

      // ÁõëÂê¨ËøûÊé•Áä∂ÊÄÅ
      socket.addEventListener('open', () => {
        if (options.debug) {
          console.log('üåê sharedb: WebSocket connected');
        }
      });

      socket.addEventListener('close', () => {
        setOption('_status', 'disconnected');
        options.onStatusChange?.('disconnected');
        options.onDisconnect?.();
        
        if (options.debug) {
          console.log('‚ùå sharedb: WebSocket disconnected');
        }
      });

      socket.addEventListener('error', (event) => {
        setOption('_status', 'error');
        options.onStatusChange?.('error');
        options.onError?.(event);
        
        if (options.debug) {
          console.error('üö® sharedb: WebSocket error:', event);
        }
      });

    } catch (error) {
      setOption('_status', 'error');
      options.onStatusChange?.('error');
      options.onError?.(error);
      
      if (options.debug) {
        console.error('üí• sharedb: Connection failed:', error);
      }
      
      throw error;
    }
  },

  /**
   * Êñ≠ÂºÄËøûÊé•
   */
  disconnect: () => {
    const { getOptions, setOption } = ctx;
    const options = getOptions();

    if (options._socket) {
      options._socket.close();
      setOption('_socket', null);
    }

    if (options._connection) {
      options._connection.close();
      setOption('_connection', null);
    }

    setOption('_doc', null);
    setOption('_status', 'disconnected');
    options.onStatusChange?.('disconnected');
    options.onDisconnect?.();

    if (options.debug) {
      console.log('üîå sharedb: Disconnected from ShareDB');
    }
  },

  getCtx: () => ctx,

  /**
   * Ëé∑ÂèñÂΩìÂâçËøûÊé•Áä∂ÊÄÅ
   */
  getStatus: (): OtStatus => {
    const { getOptions } = ctx;
    return getOptions()._status;
  },

  /**
   * ÂàùÂßãÂåñ sharedb ËøûÊé•ÂíåÊñáÊ°£Áä∂ÊÄÅ
   */
  init: async ({
    id,
    autoConnect = true,
    collection = 'documents',
    reconnection,
    url = 'ws://localhost:8111',
    value,
  }: OtInitOptions = {}) => {
    const { editor, getOptions, setOption } = ctx;
    const options = getOptions();

    // ‰ΩøÁî® editor.id ‰Ωú‰∏∫ÈªòËÆ§ÊñáÊ°£ ID
    const documentId = id ?? editor.id;

    // ËÆæÁΩÆ ShareDB ÈÖçÁΩÆ
    const sharedbConfig = {
      collection,
      documentId,
      reconnection: {
        enabled: true,
        interval: 3000,
        maxRetries: 5,
        ...reconnection,
      },
      url,
    };

    // Êõ¥Êñ∞Êèí‰ª∂ÈÖçÁΩÆ
    setOption('sharedb', sharedbConfig);

    if (options.debug) {
      console.log('üîß sharedb: Initializing with config:', sharedbConfig);
    }

    // Â¶ÇÊûúÊèê‰æõ‰∫ÜÂàùÂßãÂÄºÔºåÂÖàËÆæÁΩÆÂà∞ÁºñËæëÂô®
    if (value !== null && value !== undefined) {
      let initialNodes = value as any;
      
      if (typeof value === 'string') {
        initialNodes = editor.api.html.deserialize({
          element: value,
        }) as any;
      } else if (typeof value === 'function') {
        initialNodes = await value(editor);
      } else if (value) {
        initialNodes = value;
      }
      
      if (!initialNodes || initialNodes?.length === 0) {
        initialNodes = editor.api.create.value();
      }

    }

    // Ëá™Âä®ËøûÊé•
    if (autoConnect) {
      await (ctx.api as any).sharedb.connect();
    }
  },

})); 