import type { ExtendEditor } from 'platejs';
import type { Operation } from 'slate';

import type { OtConfig } from './types';
import { castArray } from './util';

/**
 * Êâ©Â±ïÁºñËæëÂô®‰ª•ÊîØÊåÅ sharedb ÂäüËÉΩ
 */
export const withPlateOt: ExtendEditor<OtConfig> = ({ editor, getOptions }) => {
  const originalApply = editor.apply as (operation: Operation) => void;

  // ‰øùÂ≠òÁúüÊ≠£ÁöÑÂéüÂßã apply ÊñπÊ≥ï‰æõËøúÁ®ãÊìç‰Ωú‰ΩøÁî®
  (editor as any).originalApply = originalApply;
  
  // Áî®‰∫éÊ†áËÆ∞ÊòØÂê¶Ê≠£Âú®Â∫îÁî®ËøúÁ®ãÊìç‰Ωú
  // ÈáçÂÜô apply ÊñπÊ≥ïÊù•Êã¶Êà™Êú¨Âú∞Êìç‰Ωú
  editor.apply = async(operation: Operation, ) => {
    const options = getOptions();
    
    // ÂÖàÂ∫îÁî®Êìç‰ΩúÂà∞ÁºñËæëÂô®
    originalApply(operation);

    if (options.debug) {
      console.log('üîÑ sharedb: Applying remote operation:', operation);
    }
    
    // Â¶ÇÊûúËøûÊé•Â∑≤Âª∫Á´ãÔºåÊèê‰∫§Êìç‰ΩúÂà∞ ShareDB
    if (options._status === 'connected' && options._doc) {
      // ËøáÊª§ÈÄâÊã©Êìç‰ΩúÔºàShareDB ‰∏çÈúÄË¶ÅÂ§ÑÁêÜÈÄâÊã©ÂèòÂåñÔºâ
      if (operation.type === 'set_selection') {
        return;
      }

      const opFormat = castArray(operation);
      
      if (options.debug) {
        console.log('üìù sharedb: Submitting local operation:', opFormat);
      }
      
      // ÂºÇÊ≠•Êèê‰∫§Êìç‰ΩúÔºå‰∏çÈòªÂ°ûÁºñËæëÂô®
      submitOpToShareDB(options._doc, opFormat, options.debug).catch((error: any) => {
        if (options.debug) {
          console.error('‚ùå sharedb: Failed to submit operation:', error);
        }
        options.onError?.(error);
      });
    }
  };

  // ‰∏∫ËøúÁ®ãÊìç‰ΩúÊèê‰æõ‰∏ìÁî®ÁöÑÂ∫îÁî®ÊñπÊ≥ï
  (editor as any).applyRemoteOperations = (operations: Operation[]) => {
    const options = getOptions();
    
    if (options.debug) {
      console.log('üì® sharedb: Applying remote operations:', operations);
    }
    
    try {
      operations.forEach(op => {
        originalApply(op);
      });
    } catch (error) {
      if (options.debug) {
        console.error('üí• sharedb: Failed to apply remote operation:', error);
      }
      throw error;
    } 
  };

  return editor;
};

/**
 * Êèê‰∫§Êìç‰ΩúÂà∞ ShareDB ÁöÑËæÖÂä©ÂáΩÊï∞
 */
async function submitOpToShareDB(doc: any, operations: Operation[], debug?: boolean): Promise<void> {
  if (!doc) return;

  try {
    await new Promise<void>((resolve, reject) => {
      const filteredOps = operations.filter(op => op && op.type !== 'set_selection');
      
      if (filteredOps.length === 0) {
        resolve();
        return;
      }

      if (debug) {
        console.log('üì§ sharedb: Submitting operations to ShareDB:', filteredOps);
      }

      // ‰ΩøÁî® source: false Ê†áËØÜËøôÊòØÊú¨Âú∞Êìç‰Ωú
      doc.submitOp(filteredOps, { source: false }, (error: any) => {
        if (error) {
          if (debug) {
            console.error('‚ùå sharedb: ShareDB operation submit failed:', error);
          }
          reject(error);
        } else {
          if (debug) {
            console.log('‚úÖ sharedb: Operation submitted to ShareDB successfully');
          }
          resolve();
        }
      });
    });
  } catch (error) {
    if (debug) {
      console.error('üí• sharedb: Submit operation error:', error);
    }
    throw error;
  }
} 