import type { Operation } from 'slate';

/**
 * Êìç‰ΩúÈòüÂàóÈÖçÁΩÆ
 */
export interface OperationQueueOptions {
  /** Èò≤ÊäñÂª∂ËøüÔºàÊØ´ÁßíÔºâ */
  debounceMs?: number;
  /** ÊúÄÂ§ßÊâπÊ¨°Â§ßÂ∞è */
  maxBatchSize?: number;
}

/**
 * Êìç‰ΩúÈòüÂàóÁ±ª - Áî®‰∫éÊâπÂ§ÑÁêÜÂíåÈò≤ÊäñÊìç‰ΩúÊèê‰∫§
 */
export class OperationQueue {
  private queue: Operation[] = [];
  private processing = false;
  private timeoutId: NodeJS.Timeout | null = null;
  private options: Required<OperationQueueOptions>;

  constructor(
    private submitFn: (operations: Operation[]) => Promise<void>,
    options: OperationQueueOptions = {}
  ) {
    this.options = {
      debounceMs: 100,
      maxBatchSize: 50,
      ...options,
    };
  }

  /**
   * Ê∑ªÂä†Êìç‰ΩúÂà∞ÈòüÂàó
   */
  enqueue(operations: Operation | Operation[]) {
    const ops = Array.isArray(operations) ? operations : [operations];
    
    // ËøáÊª§ÊéâÈÄâÊã©Êìç‰Ωú
    const contentOps = ops.filter(op => op.type !== 'set_selection');
    if (contentOps.length === 0) {
      return;
    }

    this.queue.push(...contentOps);

    // Â¶ÇÊûúÈòüÂàóËææÂà∞ÊúÄÂ§ßÂ§ßÂ∞èÔºåÁ´ãÂç≥Â§ÑÁêÜ
    if (this.queue.length >= this.options.maxBatchSize) {
      this.processImmediately();
    } else {
      this.scheduleProcess();
    }
  }

  /**
   * Á´ãÂç≥Â§ÑÁêÜÈòüÂàó
   */
  private processImmediately() {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
      this.timeoutId = null;
    }
    this.process();
  }

  /**
   * ËÆ°ÂàíÂ§ÑÁêÜÈòüÂàóÔºàÈò≤ÊäñÔºâ
   */
  private scheduleProcess() {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
    }

    this.timeoutId = setTimeout(() => {
      this.timeoutId = null;
      this.process();
    }, this.options.debounceMs);
  }

  /**
   * Â§ÑÁêÜÈòüÂàó
   */
  private async process() {
    if (this.processing || this.queue.length === 0) {
      return;
    }

    this.processing = true;

    try {
      const operations = this.queue.splice(0, this.options.maxBatchSize);
      await this.submitFn(operations);
    } catch (error) {
      console.error('Failed to process operation queue:', error);
    } finally {
      this.processing = false;

      // Â¶ÇÊûúËøòÊúâÊìç‰ΩúÂú®ÈòüÂàó‰∏≠ÔºåÁªßÁª≠Â§ÑÁêÜ
      if (this.queue.length > 0) {
        this.scheduleProcess();
      }
    }
  }

  /**
   * Ê∏ÖÁ©∫ÈòüÂàó
   */
  clear() {
    this.queue = [];
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
      this.timeoutId = null;
    }
  }

  /**
   * Ëé∑ÂèñÈòüÂàóÂ§ßÂ∞è
   */
  get size() {
    return this.queue.length;
  }

  /**
   * Ê£ÄÊü•ÊòØÂê¶Ê≠£Âú®Â§ÑÁêÜ
   */
  get isProcessing() {
    return this.processing;
  }
} 